/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@keyframes fadeInCatalogMenu {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: none;
    opacity: 1;
  }
}

.palisandr-header-space,
.palisandr-header {
  height: 60px;
}

.palisandr-header {
  font-family: Gotham Pro;
  background: #fff;
  color: #000;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.3);
}

.palisandr-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}

.palisandr-header .main-menu-cont {
  position: relative;
  display: none;
}

@media only screen and (min-width: 768px) {
  .palisandr-header .main-menu-cont {
    text-align: left;
  }
}

@media only screen and (min-width: 1024px) {
  .palisandr-header .main-menu-cont {
    height: inherit;
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
  }
}

@media only screen and (max-width: 1023px) {
  .opened-mobile-main-menu .palisandr-header .main-menu-cont {
    display: block;
    position: absolute;
    width: 100vw;
    max-width: 375px;
    background: #fff;
    top: 100%;
    animation: fadeInCatalogMenu .3s ease;
    animation-fill-mode: both;
    z-index: 10;
    right: 0;
    left: 0;
  }
  .opened-mobile-main-menu .palisandr-header .main-menu-cont a {
    display: block;
    padding: 10px 15px;
  }
}

.palisandr-header .header-nav-item-cont {
  position: relative;
}

@media only screen and (min-width: 1024px) {
  .palisandr-header .header-nav-item-cont {
    display: flex;
    align-items: center;
  }
}

.palisandr-header .have-arrow > .header-nav-item::after {
  content: '>';
  transform: rotate(90deg) scale(1, 1.6);
  display: inline-block;
  margin-left: 15px;
}

@media only screen and (min-width: 1024px) {
  .palisandr-header .header-nav-item:hover + .sub-menu,
  .palisandr-header .sub-menu:hover {
    display: block;
  }
}

.palisandr-header .sub-menu {
  padding-left: 25px;
  top: 100%;
  background: #fff;
}

@media only screen and (min-width: 1024px) {
  .palisandr-header .sub-menu {
    display: none;
    position: absolute;
    top: 100%;
    padding-left: 0;
    background: #fff;
    animation: fadeInCatalogMenu .2s ease;
  }
}

@media only screen and (min-width: 1024px) {
  .palisandr-header .sub-menu .sub-menu {
    top: 0;
    left: 100%;
  }
}

.palisandr-header .header-nav-item {
  font-size: 15px;
  color: #000;
  margin-left: 8px;
  margin-right: 8px;
  padding: 18px 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transperent;
  white-space: nowrap;
}

@media only screen and (min-width: 1200px) {
  .palisandr-header .header-nav-item {
    margin-left: 13px;
    margin-right: 13px;
    font-size: 16px;
  }
}

.palisandr-header .header-nav-item:hover {
  border-top: 6px solid #f7d89c;
}

.palisandr-header .header-number {
  text-align: right;
  font-weight: 600;
}

.palisandr-header .header-number a {
  font-size: 18px;
  color: #000;
  transition: color .2s ease;
}

.palisandr-header .header-number a:hover {
  color: #dab086;
}

.palisandr-header a.header-item-text {
  font-size: 15px;
  color: #dab086;
  text-decoration: underline;
  height: 40px;
  font-weight: 300;
  transition: color .2s ease;
}

.palisandr-header a.header-item-text:hover {
  color: #000;
}

.open-main-menu {
  display: block;
  padding: 6px;
}

@media only screen and (min-width: 1024px) {
  .open-main-menu {
    display: none;
  }
}

.open-main-menu .icon-menu {
  width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.open-main-menu .icon-menu span {
  display: block;
  width: 100%;
  height: 3px;
  background: #dab086;
  margin-bottom: 6px;
  transition: transform .2s, opacity .2s ease;
}

.open-main-menu .icon-menu span:nth-of-type(3) {
  margin-bottom: 0;
}

.opened-mobile-main-menu .open-main-menu .icon-menu span:nth-of-type(1) {
  transform: rotate(-135deg);
