[class*='cusmenu-icon'] {
  position: relative;
}

[class*='cusmenu-icon']::before, [class*='cusmenu-icon']::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  transition: all .2s ease;
  background: center no-repeat transparent;
}

[class*='cusmenu-icon']::before {
  opacity: 1;
}

[class*='cusmenu-icon']::after {
  opacity: 0;
}

[class*='cusmenu-icon']:hover::before {
  opacity: 0;
}

[class*='cusmenu-icon']:hover::after {
  opacity: 1;
}

